.NavBar {
    background-color: #000000;
    overflow: hidden;
    margin: 0;
    padding: 0;
  
    & a {
      display: block;
      float: left;
      color: #f2f2f2;
      text-align: center;
      padding: 14px 16px;
      text-decoration: none;
      font-size: 17px;
    }
    
    & a:hover {
      background-color: #ddd;
      color: black;
    }
  }